<template>
  <div>  
    <!-- ============================================================== -->
    <!-- Preloader - style you can find in spinners.css -->
    <!-- ============================================================== -->
    <div class="preloader" v-bind:class="{hide_elem: show_preloader}">
        <svg class="circular" viewBox="25 25 50 50">
            <circle class="path" cx="50" cy="50" r="20" fill="none" stroke-width="2" stroke-miterlimit="10" /> </svg>
    </div>
    <!-- ============================================================== -->
    <!-- Main wrapper - style you can find in pages.scss -->
    <!-- ============================================================== -->
    <section id="wrapper" class="login-register login-sidebar" :style="{backgroundImage: `url(${require('@/assets/images/background/login-register.jpg')})` }" >
  <div class="login-box card">
    <div class="card-body">
      <form class="form-horizontal form-material" id="loginform" action="index.html">
        <router-link href="/index" to="" class="text-center db"><img src="../assets/images/logo-icon.png" alt="Home" /><br/><img src="../assets/images/logo-text.png" alt="Home" /></router-link> 
        <h3 class="box-title mt-5 mb-0">Регистрация</h3><small>создайте свой аккаунт</small>
        <div class="form-group mt-3" >
          <div class="col-xs-12" >
            <input class="form-control" v-model="first_name" type="text" required="" placeholder="Имя" v-bind:class="{error: error1}">
          </div>
        </div>
        <div class="form-group mt-3" >
          <div class="col-xs-12" >
            <input class="form-control" v-model="last_name" type="text" required="" placeholder="Фамилия" v-bind:class="{error: error2}">
          </div>
        </div>
        <div class="form-group ">
          <div class="col-xs-12">
            <input class="form-control" v-model="email" type="text" required="" placeholder="Email" v-bind:class="{error: error3}">
          </div>
        </div>
        <div class="form-group ">
          <div class="col-xs-12">
            <input class="form-control" v-model="pass1" type="password" required="" placeholder="Пароль" v-bind:class="{error: error4}">
          </div>
        </div>
        <div class="form-group">
          <div class="col-xs-12">
            <input class="form-control" v-model="pass2" type="password" required="" placeholder="Повторите пароль" v-bind:class="{error: error4}">
          </div>
        </div>
        <div class="form-group">
          <div class="col-md-12" v-bind:class="{error: error5}">
            <div class="checkbox checkbox-primary pt-0" >
              <input v-model="checkbox" id="checkbox-signup" type="checkbox">
              <label for="checkbox-signup"> Согласен со всеми <router-link to="#">Условиями</router-link></label>
            </div>
          </div>
        </div>
        <span :style="{color: 'red'}" v-if="error_response == 'profile with this email already exists.'" v-bind:class="{hide_elem: error_r}">
          Пользователь с таким E-mail уже зарегестрирован!
        </span>
        <div class="form-group text-center mt-3">
          <div class="col-xs-12">
            <span v-on:click="profileFunc"><router-link to='' class="btn btn-info btn-lg btn-block text-uppercase waves-effect waves-light">Зарегистрироваться</router-link></span>
          </div>
        </div>
        <div class="form-group mb-0">
          <div class="col-sm-12 text-center">
            <p>Зарегистрированы? <router-link to="/login" class="text-info ml-1"><b>Войти</b></router-link></p>
          </div>
        </div>
      </form>
    </div>
  </div>
</section>
  </div>
</template>

<style code="css">
    @import url('~@/assets/style/css/style.css');
    @import url('~@/assets/style/css/colors/red-dark.css');
    @import url('~@/assets/plugins/bootstrap/css/bootstrap.min.css');
    @import url('~@/assets/plugins/datatables.net-bs4/css/dataTables.bootstrap4.css');

</style>

<script>
    import {profile} from '@/service/profile'

    export default {
        data() {
            return {
                first_name: '',
                last_name: '',
                email: '',
                pass1: '',
                pass2: '',

                error1: false,
                error2: false,
                error3: false,
                error4: false,
                error5: false,

                show_preloader: true,
                checkbox: false,

                error_response: '',
                error_r: true,

            }
        },
        methods: {
            profileFunc() {
                let data = {
                    first_name: this.first_name,
                    last_name: this.last_name,
                    email: this.email,
                    password: this.pass1
                }

                if (this.first_name.length === 0) {
                    this.error1 = true;
                    setTimeout(() => {this.error1 = false}, 2000)
                }
                if (this.last_name.length === 0) {
                    this.error2 = true;
                    setTimeout(() => {this.error2 = false}, 2000)
                }
                 if (this.email.length === 0) {
                    this.error3 = true;
                    setTimeout(() => {this.error3 = false}, 2000)
                }
                 if (this.pass1.length === 0 || this.pass2.length === 0) {
                    this.error4 = true;
                    setTimeout(() => {this.error4 = false}, 2000)
                }
                if (this.checkbox) {
                    if (this.pass1 === this.pass2) {
                        this.show_preloader = false;
                        profile(data).then(res => {
                            this.$router.push('/')
                        })
                        .catch(err => {
                          this.first_name = '';
                          this.last_name = '';
                          this.email = '';
                          this.pass1 = '';
                          this.pass2 = '';
                          this.error_response = err.response.data.email[0]
                          this.error_r = false;
                          setTimeout(() => {this.error_r = true}, 2000)
                        })
                    } else {
                        this.error4 = true;
                        setTimeout(() => {this.error4 = false}, 2000)
                    }
                } else {
                    this.error5 = true;
                    setTimeout(() => {this.error5 = false}, 2000)
                }
                
            }, 
        },
    }
</script>
